import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './model/home/home.component';
import { ShopComponent } from './model/shop/shop.component';
const authModule = () => import('./model/auth/auth.module').then(x => x.AuthModule);
const forumModule = () => import('./model/forum/forum.module').then(x => x.ForumModule);
const AccountModule = () => import('./model/account/account.module').then(x => x.AccountModule);
import { AuthGuard } from './_helpers';

const routes: Routes = [
    { path: '', component: HomeComponent},
    { path: 'auth', loadChildren: authModule },
    { path: 'forum', loadChildren: forumModule },
    { path: 'shop', component: ShopComponent},
    { path: '', loadChildren: AccountModule },

    // otherwise redirect to home
    { path: '**', redirectTo: '' }
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule { }