import {Component, ElementRef, OnInit, ViewChild, Input} from '@angular/core';
import {EngineServiceGta} from './engine/engineGta.service';
import {showModels} from './engine/showModel.service';
import {showModelsAccount} from './engine/showModelAccount.service';

@Component({
  selector: 'app-web-gl',
  templateUrl: './web-gl.component.html',
  styleUrls: ['./web-gl.component.less']
})
export class WebGLComponent implements OnInit {
  @Input() models: any;
  @ViewChild('rendererCanvas', {static: true})
  public rendererCanvas!: ElementRef<HTMLCanvasElement>;

  public constructor(private engServ: EngineServiceGta, private showModel: showModels, private showModelAccount: showModelsAccount) 
  {    
  }

  public ngOnInit(): void {
    if(this.models == 'gta') {
      this.engServ.createScene(this.rendererCanvas, this.models);
      this.engServ.animate();
    } else if(this.models == 'skins') {
      this.showModel.createScene(this.rendererCanvas, this.models);
      this.showModel.animate();
    } else if(this.models == 'skinsAccount') {
      this.showModelAccount.createScene(this.rendererCanvas, this.models);
      this.showModelAccount.animate();
    }
  }

}