import * as THREE from 'three';
import {ElementRef, Injectable, NgZone, OnDestroy} from '@angular/core';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

@Injectable({providedIn: 'root'})
export class EngineServiceGta implements OnDestroy {
  private canvas: HTMLCanvasElement;
  private renderer: THREE.WebGLRenderer;
  private camera: THREE.PerspectiveCamera;
  private scene: THREE.Scene;
  private light: THREE.AmbientLight;

  private models: string;

  private clock: THREE.Clock;
  private mixer;

  private frameId: number = null;
  model: any;

  public constructor(private ngZone: NgZone) {
  }

  public ngOnDestroy(): void {
    if (this.frameId != null) {
      cancelAnimationFrame(this.frameId);
    }
  }

  public createScene(canvas: ElementRef<HTMLCanvasElement>, models): any {
    this.canvas = canvas.nativeElement;
    this.models = models;
    this.renderer = new THREE.WebGLRenderer({
      canvas: this.canvas,
      alpha: true,    // transparent background
      antialias: true // smooth edges
    });
    this.renderer.setSize(window.innerWidth, window.innerHeight);

    this.clock = new THREE.Clock();

    // create the scene
    this.scene = new THREE.Scene();

    this.camera = new THREE.PerspectiveCamera(
      75, window.innerWidth / window.innerHeight, 0.1, 1000
    );

    this.camera.position.set( 2, 1, 0.5 );
    this.camera.rotation.x = 0;
    this.camera.rotation.y = 2;
    this.camera.rotation.z = 0;

    this.scene.add(this.camera);

    // const controls = new OrbitControls( this.camera, this.canvas);

    // soft white light
    const light = new THREE.PointLight( 0xffffff, 3, 500 );
    light.position.set( 1, 0, 0 );
    this.scene.add( light );
    let testscene = this.scene;

    const loader = new GLTFLoader();
    loader.load(
      // resource URL
      './assets/models/gta/scene.gltf',
      // called when the resource is loaded
      (gltf) => {
        gltf.scene.position.z = 0;
        gltf.scene.position.x = 0;
        gltf.scene.position.y = 0;
        this.model = gltf.scene;
        if(this.model) {
          this.model.rotation.y += 1.5;
        }

        const animations = gltf.animations;
    
        this.mixer = new THREE.AnimationMixer( this.model );
    
        const action = this.mixer.clipAction( animations[ 0 ] ); // play the first animation
        action.play();

        testscene.add(this.model);
  

    
      },
      // called while loading is progressing
      function ( xhr ) {
    
        console.log( ( xhr.loaded / xhr.total * 100 ) + '% loaded' );
    
      },
      // called when loading has errors
      function ( error ) {
    
        console.log( 'An error happened' );
    
      }
    );
  }

  public animate(): void {
    // We have to run this outside angular zones,
    // because it could trigger heavy changeDetection cycles.
    this.ngZone.runOutsideAngular(() => {
      if (document.readyState !== 'loading') {
        this.render();
      } else {
        window.addEventListener('DOMContentLoaded', () => {
          this.render();
        });
      }

      window.addEventListener('resize', () => {
        this.resize();
      });
    });
  }

  public render(): void {
    this.frameId = requestAnimationFrame(() => {
      this.render();
    });

    if(this.model) {
      const delta = this.clock.getDelta();
      this.mixer.update(delta);
    }

    
    this.renderer.render(this.scene, this.camera);
  }

  public resize(): void {
    const width = window.innerWidth;
    const height = window.innerHeight;

    this.camera.aspect = width / height;
    this.camera.updateProjectionMatrix();

    this.renderer.setSize(width, height);
  }
}