import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { AppRoutingModule } from './app-routing.module';
import { JwtInterceptor, ErrorInterceptor } from './_helpers';
import { AppComponent } from './app.component';
import { HomeComponent } from './model/home/home.component';
import { HeaderComponent } from './model/section/header/header.component';
import { FooterComponent } from './model/section/footer/footer.component';

import { AlertComponent } from './model/section/alert/alert.component';

import { LoadingBarRouterModule } from '@ngx-loading-bar/router';
import { NgxSkeletonLoaderModule } from 'ngx-skeleton-loader';

import { fakeBackendProvider } from './_helpers';

import { sharedModule } from './shared.module';
import { ShopComponent } from './model/shop/shop.component';


@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    HeaderComponent,
    FooterComponent,
    AlertComponent,
    ShopComponent
  ],
  imports: [
    BrowserModule,
    ReactiveFormsModule,
    HttpClientModule,
    AppRoutingModule,
    LoadingBarRouterModule,
    NgxSkeletonLoaderModule,
    sharedModule,
    BrowserAnimationsModule
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },

    fakeBackendProvider,
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
